@font-face {
  font-family: 'Source Sans Pro';
  src: url('../../../../vendor/fonts/sourcesanspro-regular.woff') format('woff');
}

.NewsCard__date{
  margin: 20px auto 0 24px;
  font-family: 'Source Sans Pro', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 24px;
  color: #B6BCBF;
}

@media (max-width: 894px) {
  .NewsCard__date{
    margin: 16px auto 12px 16px;
  }
}

@media (max-width: 555px) {
  .NewsCard__date{
    margin: 20px auto 0 24px;
    font-size: 18px;
    line-height: 24px;
  }
}

@media (max-width: 400px) {
  .NewsCard__date{
    margin: 16px 16px 10px;
  }
}